.toolBtn {
    height: 100%;
    width: 180px;
    cursor: pointer;
    font-size: 20px;
    text-align: center;
    position: relative;
    letter-spacing: 4px;
    font-weight: 600;
    font-family: Inter, 'sans-serif';
}

.toolBtn:after {
    content: '';
    width: 90px;
    height: 2px;
    position: absolute;
    top: 70px;
    left: 45px;
    background-color: #000;
}