.createModal{
    background-color: #252525;
    border-radius: 35px;
    width: 450px;
    height: min-content;
    padding: 30px;
}

.createItemTitle{
    font-size: 22px;
    font-weight: bold;
    color: white;
    margin-bottom: 20px;
}

.createModalText{
    font-size: 20px;
    color: white;
}

.textAreaBlock{
    width: 100%;
    height: 92px;
    padding: 10px 14px 10px 18px;
    border-radius: 25px;
    border: 2px solid white;
    margin-bottom: 15px;
}

.textArea{
    width: 100%;
    height: 100%;
    color: white;
    font-size: 20px;
}

::placeholder{
    color: #838383;
}

::-webkit-scrollbar{
    width: 6px;
}

::-webkit-scrollbar-track{
    background: none;
}

::-webkit-scrollbar-thumb{
    background-color: #fff;
    border-radius: 8px;
    visibility: hidden;
}

.textAreaBlock:hover ::-webkit-scrollbar-thumb{
    visibility: visible;
}


