.searchBar{
    background-color: #fff;
    height: 46px;
    width: 650px;
    border-radius: 100px;
    padding: 0 60px 0 18px;
    font-size: 20px;
    position: relative;

}

.searchInput{
    width: 100%;
    height: 100%;
}

.searchIcon{
    content: '';
    height: 36px;
    width: 36px;
    position: absolute;
    top: 5px;
    bottom: 0;
    right: 12px;
    background: url("../../../images/icons/лупа.png") 100% 100% /cover no-repeat;
}

input::-webkit-input-placeholder { color: #838383; }